import React from "react"
import lottery from "../images/lottery.jpg"
import minimumwidth from "../images/minimumwidth.png"
import zeroinit from "../images/zeroinit.png"
import constructive from "../images/constructive.jpg"


function Interest({children, title, img, papers, reverse}) {

    return (
        <div className="col-sm-12 col-lg-6" style={{
            display: "flex", flexDirection: "column",
            justifyContent: "space-between"
        }}>
            <h4 className="card-title card-title-color">{title}</h4>
            <div className="card">
                <div className="card-body">

                    <div className="card-text text-justify">{children}</div>

                </div>

            </div>
            <ul className={'my-4'} style={{listStyle: "none", flexGrow: 2}}>
                {papers.map((value, index) => {
                    return (
                        <li key={index}>
                            {value}
                        </li>)
                })}
            </ul>


        </div>
    )
}

function Paper({children, title, authors, link}) {
    return (
        <div>
            <div style={{lineHeight: "0.8"}}>
                <a style={{fontSize: "small"}} href={link}>{title}</a>
            </div>
            <p style={{fontSize: "x-small", textAlign: "right"}}>{authors.join(', ')}</p>
        </div>
    )
}


export default function Interests() {
    let lotteryPapers = [
        <Paper title='The Lottery Ticket Hypothesis: Finding Sparse, Trainable Neural Networks'
               authors={["Jonathan Frankle", "Michael Carbin"]}
               link={"https://arxiv.org/abs/1803.03635"}/>,
        <Paper title='Deconstructing Lottery Tickets: Zeros, Signs, and the Supermask'
               authors={["Hattie Zhou", "Janice Lan", "Rosanne Liu", "Jason Yosinski"]}
               link={"https://arxiv.org/abs/1905.01067"}/>,
        <Paper title="Rigging the Lottery: Making All Tickets Winners"
               authors={["Utku Evci", "Trevor Gale", "Jacob Menick", "Pablo Samuel Castro", "Erich Elsen"]}
               link={"https://arxiv.org/abs/1911.11134"}/>
    ];
    let minimumPapers = [
        <Paper title='Dying ReLU and Initialization: Theory and Numerical Examples'
               authors={["Lu Lu", "Yeonjong Shin", "Yanhui Su", "George Em Karniadakis"]}
               link={"https://arxiv.org/abs/1903.06733"}/>,
        <Paper title="Capacity allocation through neural network layers"
               authors={["Jonathan Donier"]}
               link={"https://arxiv.org/abs/1902.08572"}/>
    ];
    let zeroinitPapers = [];
    let constructivePapers = [
        <Paper
            title='A Constructive Approach for One-Shot Training of Neural Networks Using Hypercube-Based Topological Coverings'
            authors={["W. Brent Daniel", "Enoch Yeung"]}
            link={"https://arxiv.org/abs/1901.02878"}/>,
    ];

    return (
        <div className="container">
            <h3 id="interests" className={"section-title"} style={{textAlign: `center`}}>Interests</h3>

            <div className="separator pt-3">
                {/*<p>Here I provide with a bit of context of my current interests</p>*/}
                <div className={"card-deck my-5"}>

                    <Interest title={'Lottery Ticket'} img={lottery} papers={lotteryPapers} reverse={false}>
                        <p>The Lottery Ticket Hypothesis conjectures that all randomly initialized neural networks
                            contain a subnetwork that attains the performance than the oritinal one. This network can be
                            found using traditional pruning techniques.</p>

                        <p>The interesting part is that this subnetwork can match and even surpass the test accuracy
                            of the original network when retrained, if reinitialized with the same weights
                            than the original one. If randomly reinitialized it won't work. Therefore, they conclude
                            that all that matters is to get the correct initialization by sheer luck, hence the name
                            "Winning Lottery Ticket" (LT).</p>

                        <p>I find this phenomenon most interesting as it shows that
                            overparametrization is only required at initialization time. Therefore, it opens
                            possibilities for massive savings if we find the way train them from scratch.</p>
                    </Interest>
                    <Interest title={"NAS and Constructive Networks"} img={constructive} papers={constructivePapers}
                              reverse={true}>
                        <p>Neural architecture search (NAS) is a technique for automating the
                            design of neural networks. This means no more choosing among the plethora of architectural
                            hyperparameters: number and width of layers, connectivity, residual or dense connections,
                            activation functions, and so on. However, instead of the traditional approach based either
                            on Reinforcement Learning or Genethic algorithms I prefer something less stochastic:
                            constructive algorithms. </p>
                        <p> The objective would be something like Online-SVM, where you start from zero
                            and keep adding supports
                            automatically until you reach a certain level of complexity directly determined by the
                            regularization term. In the case of Deep Learning it would mean to keep adding units
                            precisely initialized in the optimal position of the network until convergence is
                            reached.
                        </p>
                        <p>Note that this problem is <i>very hard</i> and it is far from being solved, but I find it
                            very stimulating as a final aim.</p>
                    </Interest>
                    <div className="w-100 d-none d-sm-block d-md-none"></div>
                    <div className="w-100 d-none d-md-block d-lg-none"></div>
                    <div className="w-100 d-none d-sm-block d-md-none"></div>
                    <div className="w-100 d-none d-lg-block d-xl-none"></div>
                    <div className="w-100 d-none d-xl-block"></div>
                    <Interest title={"Minimum Width Networks"} img={minimumwidth} papers={minimumPapers} reverse={true}>
                        <p>
                            Minimum Width Networks whose layers have the minimum number of units required to solve the
                            problem. It is a first step towards finding a minimal neural network like described earlier.
                            Choosing the
                            number of units per layer is still an open problem in Deep Learning. Some guidelines have
                            been proposed by using heuristics like EfficientNet or SimpNet, but the decision
                            on the number of units is based empirically on test error ultimately.
                        </p>
                        <p>
                            My aim is to find a way to characterize the capacity of the number of units
                            (and hopefully layers) to match it to the complexity required by the dataset. In order
                            to do so I have to find why it is so difficult to train thin neural networks, although thanks
                            to the LT Hypothesis we know that they should work.
                        </p>

                    </Interest>

                    <Interest title={"Zero Initialization"} img={zeroinit} papers={zeroinitPapers} reverse={false}>
                        <p>In order to implement a constructive algorithm it is desirable to initialize weights to zero.
                            However, this is not possible with current algorithms used in Deep Learning.</p>
                        <p>Additionally, I am under the impression that random initialization places an inductive bias
                            that
                            I find poorly justified: why initialize using one distribution or another
                            (further than exploding/vanishing gradient reasons),
                            in addition to increasing the norm of the weights unnecessarily.</p>
                        <p>
                            Therefore, my aim is to develop a method to enable Zero Initialization, to be used
                            together with other algorithms and hopefully end once and for all the debate of which
                            inialization scheme is superior.
                        </p>

                    </Interest>
                </div>
            </div>
        </div>
    )
}


