import React from "react"
import sepcons from "../images/sepcons.jpg"
import linkedneurons from "../images/linkedneurons.jpg"
import ciarp from "../images/ciarp.jpg"
import master from "../images/master.jpg"

import {FaExternalLinkAlt as ExternalLink, FaGithub as Github, FaMediumM as Medium} from "react-icons/fa"


function Publication({children, title, authors, year, keywords, screenshot, paper_url, code_url, blog_url}) {
    let medium;
    if (blog_url !== '') {
        medium = <a className="mr-4 icon" href={blog_url}>
            <Medium/>
        </a>
    } else {
        medium = ''
    }
    let github;
    if (code_url !== '') {
        github = <a className="mr-4 icon" href={blog_url}>
            <Github/>
        </a>
    } else {
        github = ''
    }

    return (
        <div className="row my-5 ">
            <div className="col my-auto">
                <div className={"row"}>
                    <h4 className="card-title card-title-color mb-4">{title}</h4>
                    <div className="card px-4 py-2" style={{marginRight: '-4.5vw', position: "relative", zIndex: 10}}>
                        <div className="card-body">
                            <p className={'author'} style={{float: "left", color: "#888"}}>{authors.join(', ')}</p><p
                            className={'year'} style={{float: "right", color: "#888"}}>{year}</p>
                            <div style={{clear: "both"}}></div>
                            <p className="card-text text-justify">{children}</p>

                        </div>
                    </div>
                </div>
                <div className={"row"}>
                    <ul className={'code my-3 keyword-list orange-bullets'}>
                        {keywords.map((value, index) => {
                            return (
                                <li key={index} className="mr-3">
                                    {value}
                                </li>)
                        })}
                    </ul>
                </div>
                <div className={"row"}>
                    <ul className={"paper-link-list"}>
                        <li>
                            <a className="mr-4 icon" href={paper_url}>
                                <ExternalLink/>
                            </a></li>
                        <li>{github}</li>
                        <li>{medium}</li>
                    </ul>
                </div>
            </div>
            <div className={"col-xs-4 col-sm-4 col-md-5 col-lg-5 d-flex justify-content-center"}><a href={paper_url}>
                <img className="img-fluid" src={screenshot}
                     alt="{title} diagram"/>
            </a></div>

        </div>
    )
}


