                {/*year={2019}*/}
                {/*keywords={["Dead Point", "Dead Unit", "Model Scaling", "Separation Constraints"]}*/}
                {/*screenshot={sepcons}*/}
                {/*paper_url={''}*/}
                {/*code_url={''}*/}
                {/*blog_url={''}>*/}
                {/*In this article, we study a proposal that enables to train extremely thin (4 or 8 neurons per layer) and*/}
                {/*relatively deep (more than 100 layers) feedforward networks without resorting to any architectural*/}
                {/*modification such as Residual or Dense connections, data normalization or model scaling. We accomplish*/}
                {/*that*/}
                {/*by alleviating two problems. One of them are neurons whose output is zero for all the dataset, which*/}
                {/*renders*/}
                {/*them useless. This problem is known to the academic community as <i>dead neurons</i>. The other is a*/}
                {/*less*/}
                {/*studied problem, dead points. Dead points refers to data points that are mapped to zero during the*/}
